import './App.css';
import { NavBar } from './components/utils';
import { useState, useEffect } from 'react';
import axios from 'axios';
import logo from './logo.svg';

const themes = ['set1', 'set2', 'set3', 'set4', 'set5'];

function App() {
	const [isSpinning, setIsSpinning] = useState(false);

	const handleButtonClick = () => {
		handleClick();
		handleThemeToggle();
	};

	const handleClick = () => {
		setIsSpinning(true);

		setTimeout(() => {
			setIsSpinning(false);
		}, 1000); // Return to normal state after 5 seconds
	};
	const [currentThemeIndex, setCurrentThemeIndex] = useState(0);

	const handleThemeToggle = () => {
		setCurrentThemeIndex((prevIndex) => (prevIndex + 1) % themes.length);
	};
	const currentTheme = themes[currentThemeIndex];

	const [isOpen, setIsOpen] = useState(false);
	var bg;
	const handleToggle = () => {
		setIsOpen(!isOpen);
	};

	const [serverStatus, setServerStatus] = useState(null);
	useEffect(() => {
		axios
			.get('http://localhost:8000/api/serverstatus/')
			.then((response) => {
				setServerStatus(response.data.status);
			})
			.catch((error) => {
				console.error('Error fetching data:', error);
				setServerStatus('inactive');
			});
	}, []);

	if (isOpen && serverStatus === 'active') {
		bg = 'success';
	} else if (serverStatus === 'inactive') {
		bg = 'danger';
	}

	return (
		<div className={`app theme-${currentTheme}`}>
			<div className="body">
				<div className="container-fluid  header p-3 ">
					<img
						src={logo}
						className={`App-logo float-start   spinning-element ${
							isSpinning ? 'spin' : ''
						}`}
						alt="logo"
					/>
					<div className="">
						<h3>Project Powered by</h3>

						<h4>
							<button className="button-30" onClick={handleButtonClick}>
								<code>React</code>
							</button>
							&nbsp;and&nbsp;
							<button className={`button-30 bg-${bg}`} onClick={handleToggle}>
								<code>Django</code>
							</button>
						</h4>
					</div>
				</div>
				<NavBar />
			</div>
		</div>
	);
}

export default App;
