import { useState, useEffect } from 'react';
import axios from 'axios';
import { Modal, Form, Card, Button } from 'react-bootstrap';
import '../App.css';
import { gsap } from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

function Gallery() {
	const [items, setItems] = useState([]);

	useEffect(() => {
		axios
			.get('http://localhost:8000/api/gallery/')
			.then((response) => {
				setItems(response.data);
			})
			.catch((error) => {
				console.error('Error fetching data:', error);
			});
	}, []);
	useEffect(() => {
		const cards = document.querySelectorAll('.cardimg');

		cards.forEach((card) => {
			gsap.fromTo(
				card,
				{ opacity: 0, y: -50 },
				{
					opacity: 1,
					y: 0,
					scrollTrigger: {
						trigger: card,
						start: 'top 80%', // Adjust the start position as needed
						end: 'bottom top', // Adjust the end position as needed
						toggleActions: 'play restart play restart',
					},
				}
			);
		});
	}, []);

	return (
		<div className="app">
			<div className="container">
				<div className="row">
					{items.map((item, index) => (
						<div key={index} className="col-lg-4 col-md-6 mb-4">
							<Card className="cardimg">
								<Card.Img variant="top" src={item.image_url} />
								<Card.Body>
									<Card.Title className="cardtitle">{item.title}</Card.Title>
									<Card.Text className="carddescription">
										{item.description}
									</Card.Text>
								</Card.Body>
							</Card>
						</div>
					))}

					<Card className="cardimg text-center">
						<Card.Img variant="top" src="" />
						<Card.Body>
							<Card.Title className="cardtitle">Post Image here</Card.Title>
							<Card.Text className="carddescription">
								<UploadModal />
							</Card.Text>
						</Card.Body>
					</Card>
				</div>
			</div>
		</div>
	);
}

const ModalForm = ({ show, handleClose }) => {
	const [formData, setFormData] = useState({
		title: '',
		description: '',
		image_url: '',
	});

	const handleChange = (e) => {
		const { name, value } = e.target;
		setFormData({
			...formData,
			[name]: value,
		});
	};

	const handleSubmit = async (e) => {
		e.preventDefault();
		try {
			const response = await axios.post(
				'http://localhost:8000/api/create/',
				formData
			);
			console.log(response.data); // Successful response from Django API

			setFormData({
				title: '',
				description: '',
				image_url: '',
			});
		} catch (error) {
			console.error(error);
		}
	};
	return (
		<Modal show={show} onHide={handleClose}>
			<Modal.Header closeButton>
				<Modal.Title>Add New Item</Modal.Title>
			</Modal.Header>
			<Modal.Body>
				<Form>
					<Form.Group controlId="formTitle">
						<Form.Label>Title</Form.Label>
						<Form.Control
							type="text"
							name="title"
							className="form-control"
							value={formData.title}
							onChange={handleChange}
							placeholder="Enter title"
						/>
					</Form.Group>

					<Form.Group controlId="formDescription">
						<Form.Label>Description</Form.Label>
						<Form.Control
							name="description"
							className="form-control"
							value={formData.description}
							onChange={handleChange}
							as="textarea"
							rows={3}
							placeholder="Enter description"
						/>
					</Form.Group>

					<Form.Group controlId="formURL">
						<Form.Label>URL</Form.Label>
						<Form.Control
							type="text"
							name="image_url"
							className="form-control"
							value={formData.image_url}
							onChange={handleChange}
							placeholder="Enter URL"
						/>
					</Form.Group>
				</Form>
			</Modal.Body>
			<Modal.Footer>
				<Button variant="secondary" onClick={handleClose}>
					Close
				</Button>
				<Button variant="primary" onClick={handleSubmit}>
					Save
				</Button>
			</Modal.Footer>
		</Modal>
	);
};

function UploadModal() {
	const [showModal, setShowModal] = useState(false);
	const handleClose = () => setShowModal(false);
	const handleShow = () => setShowModal(true);

	return (
		<div className="App">
			<button className="button-30 mt-3" onClick={handleShow}>
				Post
			</button>

			<ModalForm show={showModal} handleClose={handleClose} />
		</div>
	);
}

export default Gallery;
