import '../App.css';
import { useState, useEffect } from 'react';

import { Tab, Nav } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Profile, { Homecontent } from './home.js';
import Gallery from './gallery.js';
import ContactSection from './contact.js';
//----------------------------------------------------------------//

export function NavBar() {
	const [activeTab, setActiveTab] = useState('home');

	const handleTabChange = (tabKey) => {
		setActiveTab(tabKey);
	};

	return (
		<>
			<Tab.Container
				defaultActiveKey="home"
				activeKey={activeTab}
				onSelect={handleTabChange}
			>
				<Nav variant="nav nav-pills nav-fill mb-3 sticky-top">
					<Nav.Item>
						<Nav.Link className="custom-nav-link" eventKey="home">
							Home
						</Nav.Link>
					</Nav.Item>
					<Nav.Item>
						<Nav.Link className="custom-nav-link" eventKey="profile">
							Profile
						</Nav.Link>
					</Nav.Item>
					<Nav.Item>
						<Nav.Link className="custom-nav-link" eventKey="Contact">
							Contact
						</Nav.Link>
					</Nav.Item>
					<Nav.Item>
						<Nav.Link className="custom-nav-link" eventKey="Gallery">
							Gallery
						</Nav.Link>
					</Nav.Item>
				</Nav>

				<Tab.Content>
					<Tab.Pane eventKey="home">
						<Homecontent />
					</Tab.Pane>
					<Tab.Pane eventKey="profile">
						<LoadProfile />
					</Tab.Pane>
					<Tab.Pane eventKey="Contact">
						<ContactSection />
					</Tab.Pane>
					<Tab.Pane eventKey="Gallery">
						<LoadGallery />
					</Tab.Pane>
				</Tab.Content>
			</Tab.Container>
		</>
	);
}

const LoadProfile = () => {
	const [AsyncComponent, setAsyncComponent] = useState(null);

	useEffect(() => {
		const loadAsyncComponent = async () => {
			const module = await import('./home.js');
			setAsyncComponent(module.default);
		};

		loadAsyncComponent();
	}, []); // Empty dependency array ensures the effect runs only once after mounting

	return <div>{AsyncComponent && <Profile />}</div>;
};

const LoadGallery = () => {
	const [AsyncComponent, setAsyncComponent] = useState(null);

	useEffect(() => {
		const loadAsyncComponent = async () => {
			const module = await import('./home.js');
			setAsyncComponent(module.default);
		};

		loadAsyncComponent();
	}, []); // Empty dependency array ensures the effect runs only once after mounting

	return <div>{AsyncComponent && <Gallery />}</div>;
};
